<style scoped>
  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }

  .layout-breadcrumb {
    padding: 10px 15px 0;
  }

  .layout-content {
    min-height: 200px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }

  .layout-content-main {
    padding: 10px;
  }

  .layout-copy {
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
  }

  .layout-menu-left {
    background: #464c5b;
  }

  .layout-header {
    height: 45px;
    background: #fff;
    box-shadow: 0 3px 1px rgba(0, 0, 0, .1);
    position: relative;
    z-index: 9;
  }

  .layout-logo-left {
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }

  .layout-ceiling-main a {
    color: #9ba7b5;
  }

  .layout-hide-text .layout-text {
    display: none;
  }

  .ivu-col {
    transition: width .2s ease-in-out;
  }

  .spanRight {
    position: absolute;
    right: 0;
    bottom: 0;
    top: 0;
    left: 180px;
  }

  .spanRight.ss {
    left: 50px;
  }

  .spanLeft.ss {
    width: 50px;
  }

  .spanLeft {
    width: 180px;
  }
</style>
<template>
  <div class="layout" :class="{'layout-hide-text': spanLeft }" style="height: 100%">
    <Row type="flex" style="height: 100%;">
      <div :span="spanLeft" class="layout-menu-left spanLeft" :class="{'ss':spanLeft}">
        <Menu active-name="1" theme="dark" width="auto">
          <div class="layout-logo-left"></div>
          <MenuItem name="1">
            <Icon type="ios-navigate" :size="iconSize"></Icon>
            <span class="layout-text">选项 1</span>
          </MenuItem>
          <MenuItem name="2">
            <Icon type="ios-keypad" :size="iconSize"></Icon>
            <span class="layout-text">选项 2</span>
          </MenuItem>
          <MenuItem name="3">
            <Icon type="ios-analytics" :size="iconSize"></Icon>
            <span class="layout-text">选项 3</span>
          </MenuItem>
        </Menu>
      </div>
      <div :span="spanRight" class="spanRight" :class="{'ss':spanLeft}">
        <div class="layout-header">
          <Button type="text" @click="toggleClick">
            <Icon type="navicon" size="32"></Icon>
          </Button>
        </div>
        <!--<div class="layout-breadcrumb">-->
        <!--<Breadcrumb>-->
        <!--<BreadcrumbItem href="#">首页</BreadcrumbItem>-->
        <!--<BreadcrumbItem href="#">应用中心</BreadcrumbItem>-->
        <!--<BreadcrumbItem>某应用</BreadcrumbItem>-->
        <!--</Breadcrumb>-->
        <!--</div>-->
        <div class="layout-content"
             style="position:absolute;top:45px;left: 0;right: 0;bottom:0;overflow-y: scroll;margin: 0;background:#f8f8f8;">
          <div class="layout-content-main">
            <Button type="primary" @click.native="toJson">生成JSON</Button>
            <a>{{url}}</a>
            <Table stripe border size="small" highlight-row ref="currentRowTable" :columns="columns3"
                   :data="data1"></Table>
            <Page :total="count" show-sizer @on-change="onChange" @on-page-size-change="onPageSizeChange"
                  placement="top"></Page>
            <Modal
              v-model="modal1"
              title="编辑"
              @on-ok="ok"
              @on-cancel="cancel">
              <Form :label-width="80" v-if="data1.length>0">
                <FormItem label="key">
                  <Input v-model="data1[currentIndex].key" disabled placeholder="请输入"></Input>
                </FormItem>
                <FormItem label="英文">
                  <Input v-model="data1[currentIndex].en" placeholder="请输入"></Input>
                </FormItem>
                <FormItem label="简体">
                  <Input v-model="data1[currentIndex].cn" placeholder="请输入"></Input>
                </FormItem>
                <FormItem label="繁体">
                  <Input v-model="data1[currentIndex].tn" placeholder="请输入"></Input>
                </FormItem>
              </Form>
            </Modal>
          </div>
        </div>
        <div class="layout-copy">
          2011-2016 &copy; TalkingData
        </div>
      </div>
    </Row>
  </div>
</template>
<script>
  import dataJson from '../output'
  import axios from 'axios'
  console.log(dataJson)
  // 处理数据
  // 1获取变量key
  var keys = Object.keys(dataJson.EN.message)
  console.log(keys)
  var values = Object.values(dataJson.EN.message)
  var values2 = Object.values(dataJson.CN.message)
  var values3 = Object.values(dataJson.TN.message)
  console.log(values)
  var arr = values.map((item, i) => {
    return {
      key: keys[i],
      en: item,
      cn: values2[i],
      tn: values3[i]
    }
  })
  console.log(arr)
  export default {
    data () {
      return {
        spanLeft: false,
        spanRight: 19,
        modal1: false,
        currentIndex: 0,
        url: '',
        columns3: [
          {
            type: 'index',
            width: 60,
            align: 'center'
          },
          {
            title: 'key',
            key: 'key'
          },
          {
            title: '英文',
            key: 'en'
          },
          {
            title: '中文简体',
            key: 'cn'
          },
          {
            title: '中文繁体',
            key: 'tn'
          },
          {
            title: '操作',
            key: 'action',
            fixed: 'right',
            width: 120,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, '编辑')
              ])
            }
          }
        ],
        data1: [],
        page: 1,
        limit: 10,
        count: 0
      }
    },
    computed: {
      iconSize () {
        return this.spanLeft ? 24 : 14
      }
    },
    created () {
      this._getData()
    },
    methods: {
      ok () {
        this._updateData()
      },
      cancel () {},
      show (index) {
        this.modal1 = true
        this.currentIndex = index
//        this.$Modal.info({
//          title: '用户信息',
//          content: `key：${this.data1[index].key}<br>年龄：${this.data1[index].en}<br>地址：${this.data1[index].cn}`
//        })
      },
      toggleClick () {
        this.spanLeft = !this.spanLeft
      },
      _getData () {
        axios({
          url: `http://localhost:8081/local/${this.page}/${this.limit}`,
          method: 'get'
        }).then(res => {
          console.log('1111111111111', res)
          this.data1 = res.data.data
          this.count = res.data.count
        })
      },
      _updateData () {
        let data = this.data1[this.currentIndex]
        axios({
          url: `http://localhost:8081/local/`,
          method: 'post',
          data: data
        }).then(res => {
          if (res.data.code === 0) {
            alert(res.data.msg)
          }
        })
      },
      onChange (page) {
        this.page = page
        this._getData()
      },
      onPageSizeChange (limit) {
        this.limit = limit
        this._getData()
      },
      toJson () {
        axios({
          url: `http://localhost:8081/local/all`,
          method: 'get'
        }).then(res => {
          if (res.data.code === 0) {
            let data = res.data.data
            this.url = data
            console.log(data)
            window.location.href = data
          }
        })
      }
    }
  }
</script>



